import {
	mapState
} from 'vuex'
import UniList from "@/components/uni-list/uni-list.vue"
import UniListItem from "@/components/uni-list-item/uni-list-item.vue"
export default {
	name: 'circle',
	data() {
		return {}
	},
	components: {
		UniList,
		UniListItem
	},
	computed: {
		...mapState(['user', 'defaultUrl', 'globalHeight'])
	},
	methods: {
		handleClick() {
			uni.removeStorageSync('jwt_token')
			uni.showToast({
				icon: 'none',
				position: 'top',
				title: '用户以注销'
			})
			uni.redirectTo({
				url: '../login/login'
			})
		},
		setUser() {
			uni.showModal({
				title: '提示',
				content: '修改用户资料？',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '../setUser/setUser'
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		},
		show() {
			console.log(this.user)
		}
	},
	onReady() {}
}
